.fixed_header{
    table-layout: fixed;
    border-collapse: collapse;
}

.fixed_header tbody{
  display: block;
  width: 100%;
  overflow: hidden;
  height: 200px;
}

.fixed_header tbody:hover {
  overflow: scroll;
}

.fixed_header thead tr {
   display: block;
}

.fixed_header th, .fixed_header td {
  padding: 5px;
  text-align: left;
  width: 500px;
}

.ask {
  background:honeydew;
}
  
.bid {
  background:red;
}
